* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

.main__container {
  background-color: #eff9ff;
  width: 320px;
  height: 624px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr; }

.main__tittle {
  align-self: center;
  grid-row: 1/2;
  grid-column: 1/3; }

.reasons {
  width: 290px;
  height: 30px;
  margin: 30px 15px 0 15px;
  font-family: 'roboto', sans-serif;
  font-size: 24px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.25;
  letter-spacing: normal;
  text-align: center;
  text-transform: capitalize;
  color: black; }

.main__textone {
  grid-row: 2/3;
  grid-column: 1/3; }

.main__textone--h3 {
