  font-family: 'roboto slab', sans-serif;
  font-size: 18px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.67;
  letter-spacing: normal;
  text-align: center;
  color: black; }

.main__textone--h3::first-letter {
  text-transform: uppercase; }

