  font-size: 18px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.67;
  letter-spacing: normal;
  text-align: center;
  color: black; }

.main__textone--p::first-letter {
  text-transform: uppercase; }

