header {
  background-image: url("../images/cover.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed; }

.header__container {
  display: flex;
  flex-direction: column;
  height: 100vh; }

.header__image--background {
  width: 320px;
  height: 568px;
  padding: 0 15px 53px 0; }

.header__image--burger {
  position: fixed;
  justify-content: left;
  background-color: blue;
  width: 44px;
  height: 44px;
  margin: 0 94px 240px 0;
  padding: 15px 14px; }

.header__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.header__tittle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 290px;
  height: 110px;
  padding: 284px 15px 174px 15px;
  font-family: 'roboto slab', sans-serif;
  font-size: 42px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  text-transform: uppercase;
  line-height: 1.31;
  letter-spacing: normal;
  text-align: center;
  color: white; }

.header__paragraph {
  width: 290px;
  height: 21px;
  margin: 10px 0 45px 15px;
  font-family: 'roboto', sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: white; }

.header__paragraph::first-letter {
  text-transform: uppercase; }

.header__buttondown {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  border-radius: 22.5px;
  background: white; }

.insomnia {
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 0 0 15px; }

.insomnia__text {
  width: 90px;
  height: 30px;
  margin: 0 200px 1px 0;
  font-family: roboto;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 2.14;
  letter-spacing: normal;
  text-transform: capitalize;
  color: lightblue; }

.insomnia__phrase {
  width: 290px;
  height: 60px;
  margin: 1px 0 15px;
  font-family: RobotoSlab;
  font-size: 24px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  text-transform: capitalize;
  line-height: 1.25;
  letter-spacing: normal;
  color: black; }

.insomnia__paragraph {
  width: 290px;
  height: 120px;
  margin: 15px 0 30px;
  font-family: 'roboto', sans-serif;
  font-size: 18px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.67;
  letter-spacing: normal;
  color: #4a4a4a; }

.insomnia__paragraph::first-letter {
  text-transform: uppercase; }

.insomnia__button {
  width: 76px;
  height: 45px;
  margin: 0 0 45px 15px;
  padding: 0 25px 0 26px;
  border-radius: 22.5px;
  border: solid 2px #f26002;
  font-family: 'roboto', sans-serif;
  font-size: 20px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  text-transform: capitalize;
  line-height: 2.25;
  letter-spacing: normal;
  text-align: center;
  color: #f26002;
  transition: all 2s; }

.insomnia__button:hover {
  background-color: cadetblue;
  border: black;
  color: blue; }
